import { Stage, Layer, Line, Circle, Text } from "react-konva";
import * as React from "react";
import { Map } from "immutable";
import { Vector2d } from "konva/lib/types";

interface ClockState {
  time: Date;
}

const zeroClockState = (): ClockState => ({
  time: new Date(Date.now())
});

const get_number_pos = (numeric: number, clock_radius: number, full_round: number): Vector2d => {
  let x = Math.cos(((numeric - 3) / 12) * full_round)
  let y = Math.sin(((numeric - 3) / 12) * full_round)
  let offset: number
  if (x < 0) {
    offset = 0.95
  } else {
    offset = 0.9
  }
  return { x: x * (clock_radius * offset), y: y * (clock_radius * offset) }
}

export default function Clock() {
  const [clockState, setState] = React.useState<ClockState>(zeroClockState());
  const { time } = clockState;

  React.useEffect(() => {
    const get_time = setInterval(() => {
      setState((s) => ({ ...s, time: new Date(Date.now()) }));
    }, 1000);

    return function cleanup() {
      clearInterval(get_time)
    }
  });

  const canvasWidth = 350;
  const canvasHeight = 350;

  const center_x = canvasWidth / 2;
  const center_y = canvasHeight / 2;
  const clock_radius = 160;

  const full_round = 2 * Math.PI;

  const hours = time.getHours();
  const minutes = time.getMinutes();
  const seconds = time.getSeconds();

  const hour_distance = (1 / 12) * full_round

  const hour_pos_x =
    Math.sin(((((hours - 12) / 12)) * full_round) + (hour_distance * (minutes / 60))) * (clock_radius * 0.6);
  const hour_pos_y =
    Math.cos((((hours - 12) / 12) * full_round) + (hour_distance * (minutes / 60))) * (clock_radius * 0.6);

  const minute_pos_x =
    Math.sin((minutes / 60) * full_round) * (clock_radius * 0.8);
  const minute_pos_y =
    Math.cos((minutes / 60) * full_round) * (clock_radius * 0.8);

  const seconds_pos_x =
    Math.sin((seconds / 60) * full_round) * (clock_radius * 0.8);
  const seconds_pos_y =
    Math.cos((seconds / 60) * full_round) * (clock_radius * 0.8);

  const clock_labels = Map<number, string>()
    .set(1, "I")
    .set(2, "II")
    .set(3, "III")
    .set(4, "IV")
    .set(5, "V")
    .set(6, "VI")
    .set(7, "VII")
    .set(8, "VIII")
    .set(9, "IX")
    .set(10, "X")
    .set(11, "XI")
    .set(12, "XII");

  return (
    <>
      <Stage
        className="clock"
        height={canvasHeight}
        width={canvasWidth}
        style={{
          backgroundColor: "#000000"
        }}
      >
        <Layer>
          <Circle
            x={center_x}
            y={center_y}
            radius={clock_radius}
            stroke="#FFF"
          />

          {clock_labels.keySeq().map((numeric) => {
            let pos = get_number_pos(numeric, clock_radius, full_round)
            return (
              <Text key={numeric}
                fontSize={20}
                fill="#FFF"
                text={clock_labels.get(numeric)}
                x={center_x + pos.x}
                y={center_y + pos.y}
              />
            )
          })}

          {/**Hours */}
          <Line
            x={0}
            y={0}
            stroke={"#FFF"}
            points={[
              center_x,
              center_y,
              center_x + hour_pos_x,
              center_y - hour_pos_y
            ]}
          />

          {/**Minutes */}
          <Line
            x={0}
            y={0}
            stroke={"#FFF"}
            points={[
              center_x,
              center_y,
              center_x + minute_pos_x,
              center_y - minute_pos_y
            ]}
          />

          {/**Seconds */}
          <Line
            x={0}
            y={0}
            stroke={"#e0073a"}
            points={[
              center_x,
              center_y,
              center_x + seconds_pos_x,
              center_y - seconds_pos_y
            ]}
          />
        </Layer>
      </Stage>
    </>
  );
}
